export default {
  data () {
    return {
      // 退款原因
      refundText: '',
    }
  },
  methods: {
    refund () {
      uni.showModal({
        content: '是否确定退款?',
        confirmColor: '#4EB4A0',
        success: (res) => {
          if (res.confirm) {
            uni.navigateTo({
              url: '/pages/refundDetails/refundDetails',
            })
          } else if (res.cancel) {
          }
        },
      })
    },
    // 去客服页面
    goContact () {
      uni.switchTab({
        url: '/pages/contact/contact',
      })
    },
  },
}
